<template>
    <div id="input">
        <div id="input-created" v-if="openParams">
            <div class="window" v-if="this.dialog_info.type == 1">
                <div class="window__title" v-html="dialog_info.title"></div>
                <div class="window__subtitle"></div>
                <div class="window__content">
                    <div class="window-input__text" v-html="text">
                    </div>
                    <div class="window-input">
                        <input id="input" v-model="dialogs.input.value" placeholder="Введите значение" type="text">
                    </div>
                </div>
                <div class="window__buttons" style="margin-top: 25px;">
                    <div class="window__buttons--content">
                        <div class="window-button" id="Window0"  v-on:click="okey()" v-html="dialog_info.buttons[0]"></div>
                        <div class="window-button" id="Window1" v-if="IsButtons" v-on:click="no()" v-html="dialog_info.buttons[1]"></div>
                    </div>
                </div>
            </div> 
            <div class="window" v-if="this.dialog_info.type == 0">
                <div class="window__title" v-html="dialog_info.title"></div>
                <div class="window__subtitle"></div>
                <div class="window__content">
                    <div class="window-input__text" v-html="text">
                    </div>
                </div>
                <div class="window__buttons" style="margin-top: 25px;">
                    <div class="window__buttons--content">
                        <div class="window-button" id="Window0"  v-on:click="okey()" v-html="dialog_info.buttons[0]"></div>
                        <div class="window-button" id="Window1" v-if="IsButtons" v-on:click="no()" v-html="dialog_info.buttons[1]"></div>
                    </div>
                </div>
            </div> 
            <div class="window" data-v-3e7a472e v-if="this.dialog_info.type == 2">
                <div  class="window__title-list" v-html="dialog_info.title"></div>
                <div class="window__subtitle"></div>
                <div data-v-3e7a472e class="window__content">
                    <div id="list" class="window-table">
                        <div v-html="item_filter.join('')">
                        </div>
                    </div>
                </div>
                <div class="window__buttons" style="margin-top: 25px;">
                    <div class="window__buttons--content">
                        <div class="window-button" id="Window0"  v-on:click="okey()" v-html="dialog_info.buttons[0]"></div>
                        <div class="window-button" id="Window1" v-if="IsButtons" v-on:click="no()" v-html="dialog_info.buttons[1]"></div>
                    </div>
                </div>
            </div> 
            <div class="window" v-if="this.dialog_info.type == 3">
                <div class="window__title" v-html="dialog_info.title"></div>
                <div class="window__subtitle"></div>
                <div class="window__content">
                    <div class="window-input__text" v-html="text">
                    </div>
                    <div class="window-input">
                        <input id="input" v-model="dialogs.input.value" placeholder="Введите значение" type="password">
                    </div>
                </div>
                <div class="window__buttons" style="margin-top: 25px;">
                    <div class="window__buttons--content">
                        <div class="window-button" id="Window0"  v-on:click="okey()" v-html="dialog_info.buttons[0]"></div>
                        <div class="window-button" id="Window1" v-if="IsButtons" v-on:click="no()" v-html="dialog_info.buttons[1]"></div>
                    </div>
                </div>
            </div>    
        </div>
    </div>
</template>


<script>
import emitter from 'tiny-emitter/instance'
export default {
    name:'dialogs',
    props:['openParams'],
    data(){
        return{
            dialogs:{
                input:{
                    value: ""
                }
            },
            dialog_info:{
                dialogID: -1, 
                type: -1,
                title: "Тестовый диалог",
                text: "Тут текст тестовый",
                items: [],
                buttons: ['Тест', 'Тест1'],    
                isPrivate:!1,
                header: "",
            },
            text:[],
            IsDevelopers: !0,
            open:!1,
            color: "<span style='#ffffff'>",
            TYPES: ["text", "input", "list_normal", "input_private", "list_title", "list_title"],
            item_filter: [],
            listmax: 0,
            IsButtons: !1,
        }
    },
    components:{
    },
    methods:{
        no(){
            app.sendGameEvent("OnCustomDialogResponse", this.dialog_info.dialogID, 0, -1, this.dialogs.input.value);
            emitter.emit('closed', 0);
            cef.set_focus(false)
        },
        okey(){
            app.sendGameEvent("OnCustomDialogResponse", this.dialog_info.dialogID, 1, -1, this.dialogs.input.value);
            emitter.emit('closed', 1);   
            cef.set_focus(false)
        },
        setColor(e) {
            for (let t = 0; t < e.length; t++) {
                let s = e[t];
                if ("" != s) {
                    while (null != s.match(/{(?<color>......)}/)) {
                        let e = s.match(/{(?<color>......)}/);
                        null != e && ((this.color = '<span style="color: #' + e[1] + '">'), (s = s.replace(e[0], this.color)));
                    }
                    e[t] = this.color + s;
                }
            }
            return e;
        },
        selectItem(e){
            this.select = e;
        },
    },
    created(){  
        this.$nextTick(() => {
            emitter.on('dialogs-info', (A, d, t) => {
                console.log('da')
                this.text = []; this.item_filter.length = 0;  this.item_filter = []; let message = ""; let sentences = "";  let message1 = ""; let sentences1 = ""; let list = 0, select = -1
                var e = JSON.parse(A);
                if(t == 2){
                    var a = {
                        dialogID: d, 
                        type: t,
                        title: e[0],
                        items: e[1],
                        buttons: [e[2], e[3]],     
                    }
                }
                else
                {
                    var a = {
                        dialogID: d, 
                        type: t,
                        title: e[0],
                        text:e[1],
                        buttons: [e[2], e[3]],         
                    }
                }
                this.dialog_info = a;
                if(e[3].length == 0){
                    this.IsButtons = false;
                }else{
                    this.IsButtons = true;
                }
                while(-1 != e[1].search("{")) e[1] = e[1].replace(/{(?<color>......)}/g, '<span style="color: #$<color>">')
                while(-1 != e[0].search("{")) e[0] = e[0].replace(/{(?<color>......)}/g, '<span style="color: #$<color>">')
                while (-1 != e[1].search("<n>") || -1 != e[1].search("<t>")) e[1] = e[1].replace("<n>", "<br>").replace("<t>", "                "), this.listmax++, list++;
                this.dialog_info.type = t;
                this.dialog_info.buttons[0] = e[2];
                this.dialog_info.buttons[1] = e[3]; 
                this.text = e[1]; this.dialog_info.title = e[0];           
                message = this.text;
                sentences = message.split("<br>")
                for(let i = 0; i < this.listmax;  i++ ){      
                    this.item_filter[i] = '<div id="item' + i + '" class="window-table__item" style="grid-template-columns: repeat(1, 1fr);" >' +
                    '<div class="window-table__col">' + sentences[i] + '</div>' + '</div>';      
                }  
                setTimeout(() => {
                    for(let s = 0; s < list; s++){
                        document.getElementById('item' + s).onclick = function(){          
                            app.sendGameEvent("OnCustomDialogResponse", d, 1, s, "");
                            emitter.emit('closed', 0);
                            cef.set_focus(false)
                        }
                    }       
                }, 10);    
                setTimeout(() => {
                    $('#input-created').draggable();
                    $('#input-created').fadeIn()
                }, 1);
                this.listmax = 0
            })          
        })
    },
    mounted(){  
        
    }
}
</script>

<style scoped>
@import url("../assets/style.css");
</style>