<template>
  <dialogs v-bind:openParams="openDialog"/>
</template>

<script>
import emitter from 'tiny-emitter/instance'
import dialogs from './components/dialogs.vue'
export default {
    name: 'App',
    data(){
        return{
            openDialog:!1,
            dialog_info:
            {
                dialogID: -1, 
                items:[],  
            },
            dialogIdx: -1,
            dialogsQueue: [],            
       }
    },
    methods:{
        addDialog(i, type, e = []){
            cef.set_focus(true)           
            this.dialogIdx = i; 
            if(this.openDialog == true){
                this.closeLastDialog(); 
            }
            emitter.emit('dialogs-info', e, i, type)
            delete this.e;
            this.openDialog = true;
            
        },
        closeLastDialog() {
            console.log('delete dialogs[id] :' + this.dialogIdx)
            this.dialogIdx = -1;
            this.openDialog = false;
            cef.set_focus(false)
        },
        sendGameEvent(event, ...arg){
            console.log("Event: " + event);
            console.log("Arg: " + arg);
            cef.emit(event, ...arg)
        }
    },
    created(){
        emitter.on('closed', s => {
            this.closeLastDialog()
            cef.set_focus(false)
        });
        cef.on("ShowPlayerDialog", (dialog_id, type, s) => {
            this.addDialog(dialog_id, type, s)
            cef.set_focus(true)
        });
    },
    components:{
        dialogs,
    }
}
</script>

